.group {
  position: relative;
  margin: 15px 0; }
  .group .form-input {
    outline: none;
    border-radius: 100px;
    background: #272a37;
    background-color: #272a37;
    color: #aaa9d2;
    font-size: 18px;
    padding: 10px 10px 10px 55px;
    display: block;
    width: 100%;
    height: 42px;
    border: none;
    background-repeat: no-repeat;
    background-position: 30px center;
    background-size: 12px;
    /* Safari/Chrome, other WebKit */
    /* Firefox, other Gecko */
    box-sizing: border-box; }
    .group .form-input:focus {
      outline: none; }
    .group .form-input:focus ~ .form-input-label {
      top: -5px;
      left: 30px;
      font-size: 10px;
      opacity: 1;
      color: #aaa9d2; }
  .group input[type="password"] {
    letter-spacing: 0.3em; }
  .group .form-input-label {
    color: #aaa9d2;
    font-size: 12px;
    font-weight: normal;
    position: absolute;
    align-content: center;
    pointer-events: none;
    left: 55px;
    top: 15px;
    -webkit-transition: 300ms ease all;
    transition: 300ms ease all;
    opacity: 0.5; }
    .group .form-input-label.shrink {
      top: -5px;
      left: 30px;
      font-size: 10px;
      opacity: 1;
      color: #aaa9d2; }

.custom-button {
